@use "../../scss/" as *;

.cardContainer{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 16rem;
    background-color: $gray-100;
    border-radius: 12rem;
    padding: 24rem;
    cursor: pointer;

    &.modalActive{
        background-color: inherit;
        padding: 0;
        cursor: inherit;

        .cardTitleContainer{
            .cardTitle{
                @include fnt(32, 40, $weight:600, $color: $gray-900);
            }
            .cardPrice{
                @include fnt(24, 32, $weight:400, $color: $gray-900);
            }
        }

        .containerResponses{
            background-color: $gray-100;
        }
    }

    .cardContainerBody{
        display: flex;
        flex-direction: column;
        gap: 16rem;
    }

    .cardHeaderTag{
        display: flex;
        gap: 4rem;
        overflow: scroll;

        &::-webkit-scrollbar{
            display: none;
        }
    }

    .cardTag{
        width: fit-content;
        padding: 4rem 8rem;
        border-radius: 12rem;
        border: 1rem solid $orange-dark-500;
        @include fnt(12, 16, $weight:400, $color: $orange-dark-500);

        &__type{
            width: fit-content;
            padding: 4rem 8rem;
            border-radius: 12rem;
            border: 1rem solid $orange-dark-500;    
            background-color: $orange-dark-500;
            @include fnt(12, 16, $weight:400, $color: $white);
        }
    }

   

    .cardTitleContainer{
        display: flex;
        flex-direction: column;
        gap: 8rem;

        .cardTitle{
            @include fnt(20, 24, $weight:600, $color: $gray-900);
        }

        .cardPrice{
            @include fnt(20, 24, $weight:400, $color: $gray-900);
        }
    }

    .cardDescription{
        @include fnt(16, 24, $weight:400, $color: $gray-500);
        max-height: 120rem;
        overflow: hidden;
        word-wrap: break-word;
    }

    .containerResponses {
        background-color: $white;
        width: fit-content;
        padding: 4rem 4rem 4rem 12rem;
        border-radius: 24rem;
        display: flex;
        align-items: center;
      
        .responsesAvatarts {
          width: 40rem;
          height: 40rem;
          object-fit: cover;
          border-radius: 100%;
          margin-left: -8rem;
        }

        .numberResponses{
            width: 40rem;
            height: 40rem;
            display: flex;
            justify-content: center;
            align-items: center;

            @include fnt(14, 20, $weight:400, $color: $gray-500, $font: $fontText);
        }
    }
      
}

.footerCard{
    display: flex;
    flex-direction: column;

    .footerInfoCompany{
        display: flex;
        flex-direction: column;
        gap: 4rem;

        &.active{
            .cardOrgName{
                @include fnt(20, 24, $weight:600, $color: $orange-dark-500, $font: $fontText);
            }
    
            .cardFooterDescription{
                @include fnt(16, 20, $weight:400, $color: $gray-700, $font: $fontText);
            }
            .cardFooterLink{
                @include fnt(16, 20, $weight:400, $color: $orange-dark-500, $font: $fontText);
            }
        }

       
       
    }

    .cardOrgName{
        @include fnt(16, 20, $weight:400, $color: $orange-dark-500, $font: $fontText);
    }


    .footerCardCity{
        display: flex;
        margin: 4rem 0 16rem;
        justify-content: space-between;
        align-items: center;

        .cardCity{
            @include fnt(16, 20, $weight:400, $color: $gray-500, $font: $fontText);
        }
    }
}

.whiteButtonCard{
    width: 100%;
    padding: 12rem;
    border-radius: 8rem;
    background-color: $white;
    @include fnt(16, 20, $weight:500, $color: $gray-700, $font: $fontText);
    border: 1rem solid $gray-300;

    &:hover{
        background-color: $orange-500;
        color: $white;
    }
}

.buttonContainer{
    display: flex;
    gap: 4rem;

    .whiteButtonCard{
        &:hover{
            background-color: $gray-100;
            color: $gray-900;
        }
    }
    .orangeButtonCard{
        background-color: $white;
        @include fnt(16, 20, $weight: 500, $color: $orange-dark-500);
        width: 100%;
        border: 1rem solid $orange;
        border-radius: 8rem;
        &:hover{
            background-color: $gray-100;
        }
    }

    @include tablet {
        flex-direction: column;
    }
}